import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { AlertCircle } from "lucide-react";

interface StepFormProps {
  onNext: () => void;
  formData: {
    title: string;
    message: string;
    images: FileList | null;
    counter: boolean;
  };
  setFormData: (data: any) => void;
}

export default function StepForm({ onNext, formData, setFormData }: StepFormProps) {
  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 7) {
      alert("You can only upload up to 7 images");
      return;
    }
    setFormData({ ...formData, images: files });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!formData.title || !formData.message) {
      return;
    }
    onNext();
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="space-y-2">
        <Label htmlFor="title">Title</Label>
        <Input
          id="title"
          required
          value={formData.title}
          onChange={(e) => setFormData({ ...formData, title: e.target.value })}
          placeholder="Enter the title of your memory"
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="message">Message</Label>
        <Textarea
          id="message"
          required
          value={formData.message}
          onChange={(e) => setFormData({ ...formData, message: e.target.value })}
          placeholder="Write your special message here..."
          className="min-h-[150px]"
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="images">Upload Images (up to 7)</Label>
        <Input
          id="images"
          type="file"
          multiple
          accept="image/*"
          onChange={handleImageChange}
          className="cursor-pointer"
        />
        <p className="text-sm text-gray-500">
          Select up to 7 images to include in your memory
        </p>
      </div>

      <div className="flex items-center space-x-2">
        <input
          type="checkbox"
          id="counter"
          checked={formData.counter}
          onChange={(e) => setFormData({ ...formData, counter: e.target.checked })}
          className="rounded border-gray-300"
        />
        <Label htmlFor="counter">Add a dynamic counter to your memory</Label>
      </div>

      {(!formData.title || !formData.message) && (
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>
            Please fill in all required fields (title and message)
          </AlertDescription>
        </Alert>
      )}

      <div className="mt-8">
        <Button type="submit" className="w-full text-lg py-6">
          Continue to Payment
        </Button>
      </div>
    </form>
  );
}
