interface ProgressBarProps {
  currentStep: number;
  totalSteps: number;
}

export default function ProgressBar({ currentStep, totalSteps }: ProgressBarProps) {
  return (
    <div className="w-full max-w-3xl mx-auto mb-8">
      <div className="relative">
        {/* Progress bar background */}
        <div className="h-2 bg-gray-200 rounded-full">
          {/* Progress bar fill */}
          <div
            className="h-full bg-black rounded-full transition-all duration-300"
            style={{ width: `${(currentStep / totalSteps) * 100}%` }}
          />
        </div>

        {/* Step indicators */}
        <div className="absolute top-0 left-0 w-full flex justify-between transform -translate-y-1/2">
          {[...Array(totalSteps)].map((_, index) => (
            <div
              key={index}
              className={`w-6 h-6 rounded-full border-2 flex items-center justify-center text-xs font-semibold
                ${
                  index < currentStep
                    ? "bg-black border-black text-white"
                    : index === currentStep
                    ? "bg-white border-black text-black"
                    : "bg-white border-gray-300 text-gray-500"
                }`}
            >
              {index + 1}
            </div>
          ))}
        </div>
      </div>

      {/* Step labels */}
      <div className="flex justify-between mt-4 text-sm">
        <div className="text-center flex-1">Fill in the fields</div>
        <div className="text-center flex-1">Payment</div>
        <div className="text-center flex-1">QR Code and Link</div>
        <div className="text-center flex-1">Share the Memory</div>
      </div>
    </div>
  );
}
