import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { AlertCircle, CreditCard, QrCode } from "lucide-react";

interface StepPaymentProps {
  onNext: () => void;
  onBack: () => void;
  selectedPlan: "forever" | "annual";
}

export default function StepPayment({ onNext, onBack, selectedPlan }: StepPaymentProps) {
  const [paymentMethod, setPaymentMethod] = useState<"credit" | "pix">("credit");
  const [isProcessing, setIsProcessing] = useState(false);
  const [error, setError] = useState("");

  const prices = {
    forever: { original: 8.90, discounted: 4.90 },
    annual: { original: 4.90, discounted: 2.90 }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setIsProcessing(true);

    try {
      // Simulate payment processing
      await new Promise(resolve => setTimeout(resolve, 2000));
      onNext();
    } catch (err) {
      setError("Payment processing failed. Please try again.");
    } finally {
      setIsProcessing(false);
    }
  };

  return (
    <div className="space-y-6">
      <div className="text-center">
        <div className="text-2xl font-semibold">
          {selectedPlan === "forever" ? "Forever Plan" : "Annual Plan"}
        </div>
        <div className="mt-2">
          <span className="text-gray-500 line-through mr-2">
            ${prices[selectedPlan].original.toFixed(2)}
          </span>
          <span className="text-2xl font-bold">
            ${prices[selectedPlan].discounted.toFixed(2)}
          </span>
          <span className="text-gray-600 ml-1">
            {selectedPlan === "forever" ? "/one time" : "/per year"}
          </span>
        </div>
      </div>

      <RadioGroup
        defaultValue="credit"
        onValueChange={(value) => setPaymentMethod(value as "credit" | "pix")}
        className="grid grid-cols-2 gap-4"
      >
        <div>
          <RadioGroupItem
            value="credit"
            id="credit"
            className="peer sr-only"
          />
          <Label
            htmlFor="credit"
            className="flex flex-col items-center justify-between rounded-md border-2 border-muted bg-white p-4 hover:bg-gray-50 peer-data-[state=checked]:border-black [&:has([data-state=checked])]:border-black"
          >
            <CreditCard className="mb-2 h-6 w-6" />
            Credit Card
          </Label>
        </div>

        <div>
          <RadioGroupItem
            value="pix"
            id="pix"
            className="peer sr-only"
          />
          <Label
            htmlFor="pix"
            className="flex flex-col items-center justify-between rounded-md border-2 border-muted bg-white p-4 hover:bg-gray-50 peer-data-[state=checked]:border-black [&:has([data-state=checked])]:border-black"
          >
            <QrCode className="mb-2 h-6 w-6" />
            PIX
          </Label>
        </div>
      </RadioGroup>

      {paymentMethod === "credit" ? (
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="cardNumber">Card Number</Label>
            <Input
              id="cardNumber"
              placeholder="1234 5678 9012 3456"
              required
            />
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="expiry">Expiry Date</Label>
              <Input
                id="expiry"
                placeholder="MM/YY"
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="cvc">CVC</Label>
              <Input
                id="cvc"
                placeholder="123"
                required
              />
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="name">Cardholder Name</Label>
            <Input
              id="name"
              placeholder="John Doe"
              required
            />
          </div>

          {error && (
            <Alert variant="destructive">
              <AlertCircle className="h-4 w-4" />
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          <div className="flex space-x-4">
            <Button
              type="button"
              variant="outline"
              onClick={onBack}
              className="flex-1"
            >
              Back
            </Button>
            <Button
              type="submit"
              className="flex-1"
              disabled={isProcessing}
            >
              {isProcessing ? "Processing..." : "Pay Now"}
            </Button>
          </div>
        </form>
      ) : (
        <div className="space-y-4">
          <div className="border-2 rounded-lg p-6 text-center">
            <QrCode className="w-48 h-48 mx-auto mb-4" />
            <p className="text-sm text-gray-600">
              Scan the QR code or copy the PIX key below to complete your payment
            </p>
            <div className="mt-4">
              <Input
                value="00020126580014br.gov.bcb.pix0136example-key@provider.com"
                readOnly
                className="text-center"
              />
            </div>
          </div>

          <div className="flex space-x-4">
            <Button
              type="button"
              variant="outline"
              onClick={onBack}
              className="flex-1"
            >
              Back
            </Button>
            <Button
              onClick={handleSubmit}
              className="flex-1"
              disabled={isProcessing}
            >
              {isProcessing ? "Processing..." : "I've made the payment"}
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}
