"use client";

import { useState } from "react";
import Navbar from "@/components/Navbar";
import ProgressBar from "@/components/ProgressBar";
import StepForm from "@/components/personalize/StepForm";
import StepPayment from "@/components/personalize/StepPayment";
import StepQRCode from "@/components/personalize/StepQRCode";
import StepShare from "@/components/personalize/StepShare";

export default function PersonalizePage() {
  const [currentStep, setCurrentStep] = useState(1);
  const [selectedPlan] = useState<"forever" | "annual">("forever");
  const [formData, setFormData] = useState({
    title: "",
    message: "",
    images: null,
    counter: false,
  });

  const handleNext = () => {
    setCurrentStep((prev) => Math.min(prev + 1, 4));
  };

  const handleBack = () => {
    setCurrentStep((prev) => Math.max(prev - 1, 1));
  };

  return (
    <main className="min-h-screen bg-gray-50">
      <Navbar />
      
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-16">
        <div className="mb-12">
          <h1 className="text-3xl font-bold text-center mb-8">
            Create Your Memory
          </h1>
          <ProgressBar currentStep={currentStep} totalSteps={4} />
        </div>

        <div className="bg-white rounded-lg shadow-sm border p-6">
          {currentStep === 1 && (
            <StepForm
              onNext={handleNext}
              formData={formData}
              setFormData={setFormData}
            />
          )}

          {currentStep === 2 && (
            <StepPayment
              onNext={handleNext}
              onBack={handleBack}
              selectedPlan={selectedPlan}
            />
          )}

          {currentStep === 3 && (
            <StepQRCode
              onNext={handleNext}
              onBack={handleBack}
            />
          )}

          {currentStep === 4 && (
            <StepShare
              onBack={handleBack}
            />
          )}
        </div>
      </div>
    </main>
  );
}
