import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { QrCode, Copy, Check } from "lucide-react";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface StepQRCodeProps {
  onNext: () => void;
  onBack: () => void;
}

export default function StepQRCode({ onNext, onBack }: StepQRCodeProps) {
  const [copied, setCopied] = useState(false);
  const memoryUrl = "https://memoryiit.com/m/your-unique-memory-id";

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(memoryUrl);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error("Failed to copy text: ", err);
    }
  };

  return (
    <div className="space-y-8">
      <div className="text-center">
        <h3 className="text-2xl font-semibold mb-2">
          Your Memory is Ready!
        </h3>
        <p className="text-gray-600">
          Share your unique QR code and link with your loved ones
        </p>
      </div>

      <div className="bg-white border-2 rounded-lg p-8">
        <div className="flex flex-col items-center space-y-6">
          {/* QR Code */}
          <div className="bg-white p-4 rounded-lg shadow-sm">
            <QrCode className="w-48 h-48" />
          </div>

          {/* Memory URL */}
          <div className="w-full space-y-2">
            <label className="text-sm text-gray-600">
              Your Memory Link
            </label>
            <div className="flex space-x-2">
              <Input
                value={memoryUrl}
                readOnly
                className="flex-1"
              />
              <Button
                variant="outline"
                size="icon"
                onClick={handleCopy}
                className="flex-shrink-0"
              >
                {copied ? (
                  <Check className="h-4 w-4" />
                ) : (
                  <Copy className="h-4 w-4" />
                )}
              </Button>
            </div>
          </div>
        </div>
      </div>

      <Alert>
        <AlertDescription>
          We&apos;ve also sent these details to your email for safekeeping.
        </AlertDescription>
      </Alert>

      <div className="flex space-x-4">
        <Button
          variant="outline"
          onClick={onBack}
          className="flex-1"
        >
          Back
        </Button>
        <Button
          onClick={onNext}
          className="flex-1"
        >
          Continue to Share
        </Button>
      </div>
    </div>
  );
}
